import React, {useState} from 'react'
import styled from 'styled-components'

const StyledProjectCard = styled.div`
    width: 280px;
    background-color: #734967;
    height: fit-content;
    box-shadow: 5px 5px 20px #FFB7CB;
    margin: 10px;
    position: relative;
`

const CardImage = styled.div`
display: flex;
justify-content: center;
cursor: pointer;

img {
    width: 270px;
    height: 200px;
    margin: 5px;

}
`
const CardImageOverlay = styled.div`
position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  width: 270px;
  margin: 5px;
  opacity: 0;
  transition: .5s ease;
  background-color: #FF859E;
  color: #D70170;
  font-family: 'Questrial', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;


  div {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
  }

  &:hover {
      opacity: 0.8;
  }
`

const CardDetails = styled.div`
height: 280px;
cursor: default;

  hr {
      width: 80%;
  }
`

const CardTitle = styled.div`
width: 100%;
text-align: center;
padding: 2px;
color: #FF859E;

h4 {
    margin: 0px;
}
`

const CardDescription = styled.div`
text-align: center;
padding: 5px;
`
const CardTechs = styled.div`
text-align: center;
`

const CardLink = styled.div`
text-align: center;

  a {
      text-decoration: none;
      color: #FF859E;

      &:hover {
          color: #D70170;
      }
  }
`


const ProjectCard = ({image, description, title, cardlink, techs, isENG}) => {
    const [isMore, setIsMore] = useState(false)
    return (
        <StyledProjectCard>
            <CardImage>
                <img src={image} alt="project preview"/>
                {isENG ?
                <CardImageOverlay onClick={() => {setIsMore(!isMore)}}>
                    <div>
                        {isMore ? "Close details" : "Click for more details"}
                    </div>
                </CardImageOverlay> : <CardImageOverlay onClick={() => {setIsMore(!isMore)}}>
                    <div>
                        {isMore ? "Zamknij szczegóły" : "Kliknij, by zobaczyć szczegóły"}
                    </div>
                </CardImageOverlay>
                }
            </CardImage>
            {isMore &&
                <CardDetails>
                    <CardTitle><h4>{title}</h4></CardTitle>
                    <hr></hr>
                    <CardDescription>{description}</CardDescription>
                    <hr></hr>
                    <CardTechs>{techs}</CardTechs>
                    <hr></hr>
                    <CardLink><a href={cardlink}>{isENG ? "View here" : "Zobacz tutaj"}</a></CardLink>
                </CardDetails>
                }
        </StyledProjectCard>
    )
}

export default ProjectCard
